.dialogs {
    display: grid;
    grid-template-columns: 3fr 3fr 6fr;
    font-family: Georgia;
}
.dialogsItems {
    padding: 10px;
}
.dialog.active {
    color: white;
}
.dialog.active img {
    width: 30px;
    border-radius: 50%;
    padding: 10px 10px 0 0;

}
.dialog.active.itemName {
    text-underline: none;
}
.messagesContent {
    padding: 5px;
}
.sendMessageBtn {
    composes: deleteBtn from '../../components/Profile/Myposts/Post/Post.module.css';
}
.sendMessageBtn:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 14px 40px 0 rgba(0,0,0,0.19);
}